body {
	background: #FFF;
	line-height: 1.4;
	@include roboto;
	font-size: 22px;
	min-width: 320px;
	@include xxl {
		font-size: 18px; }
	@include lg {
		font-size: 14px; }
	// &.is-fixed
	// 	+md
	// 		position: fixed
	// 		left: 0
	// 		top: 0
	// 		width: 100%
	&.is-overflow-hidden {
		overflow: hidden; } }

a {
	text-decoration: none;
	color: currentColor; }

.hero__dot {
	position: relative;
	&:before {
		content: '';
		position: absolute;
		@include vcenter;
		width: 200%;
		height: 200%; } }
